import MasterPage from "../components/Master";
import Precio from "../components/Precio";
import Noticias from "../components/Noticias";
import fetch from 'isomorphic-unfetch';

const Index = (props) => (
	<MasterPage>
		<div className="row">
			<div className="col-12">
				<h2>Precio del Bitcoin</h2>
				<Precio precio={props.precioBitcoin}/>
			</div>
			<div className="col-md-8">
				<h2>Noticias sobre Bitcoin</h2>
				<Noticias noticias={props.noticias}/>
			</div>
			<div className="col-md-4">
				<h2>Pr√≥ximos eventos Bitcoin</h2>
				<Eventos eventos={props.eventos} />
			</div>
		</div>
	</MasterPage>
);

Index.getInitialProps = async () => {
	const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
	const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&from=2019-08-30&sortBy=publishedAt&apiKey=27a0ea36dfc64b0c837e6525972ca592&language=es');
	const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=bitcoin&sort_by=date&location.address=Mexico&token=OJ3M3L63AKOHWSTEE6OH');


	const resPrecio = await precio.json();
	const resNoticias = await noticias.json();
	const resEventos = await eventos.json();

	return {
		precioBitcoin: resPrecio.data.quotes.USD,
		noticias: resNoticias.articles,
		eventos: resEventos.events
	}
}

export default Index;
